.gotop-wrapper {
  display: none;
  position: fixed;
  bottom: 8px;
  left: 8px;
}

.gotop-wrapper.show {
  display: block;
}

.gotop-link {
  transition: transform var(--transition);
  background-color: #502ee84d;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  display: block;
  scale: 1;
}

.gotop-link:before {
  content: "";
  background-color: var(--violet);
  border-radius: 50%;
  width: 54px;
  height: 54px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale3d(1, 1, 1);
}

.gotop-link:is(:hover, :focus) {
  transform: scale(1.1);
}

.gotop-icon {
