.loader {
  background: var(--violet);
  box-sizing: border-box;
  perspective: 250px;
  border-radius: 4px;
  width: 50px;
  height: 35px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: fixed !important;
}

.loader:before {
  content: "";
  background: #f5f5f5 0 0 / 15px 2.5px no-repeat;
  background-image: linear-gradient(var(--yellow) 50px, transparent 0), linear-gradient(var(--yellow) 25px, transparent 0), linear-gradient(var(--yellow) 25px, transparent 0), linear-gradient(var(--yellow) 25px, transparent 0), linear-gradient(var(--yellow) 25px, transparent 0), linear-gradient(var(--yellow) 25px, transparent 0);
  background-position: 3.5px 7px, 3.5px 14px, 3.5px 21px, 26px 7px, 26px 14px, 52.5px 45px;
  border-radius: 2px;
  position: absolute;
  inset: 2.5px;
  box-shadow: 0 0 5px #00000080;
}

.loader:after {
  content: "";
  background: #fff 0 0 / 15px 2.5px no-repeat;
  background-image: linear-gradient(var(--yellow) 25px, transparent 0), linear-gradient(var(--yellow) 25px, transparent 0), linear-gradient(var(--yellow) 25px, transparent 0);
  transform-origin: 0;
  background-position: 50% 7px, 50% 14px, 50% 21px;
  border-radius: 2px;
  width: calc(50% - 2.5px);
  animation: 1s linear infinite paging;
  position: absolute;
  top: 2.5px;
  bottom: 2.5px;
  right: 2.5px;
  transform: rotateY(0);
}

@keyframes paging {
  to {
    transform: rotateY(-180deg);
  }
}

